<template>
    <div>
        <div class="headerBox">
<!--            <el-page-header @back="goBack" content=""></el-page-header>-->
            <el-dropdown trigger="click">
                <div class="el-dropdown-link userBox" style="cursor: pointer;">
                    <img :src="userImg" alt="userimg"/>李医生<!--<i class="el-icon-arrow-right"></i>-->
                </div>
                <el-dropdown-menu slot="dropdown">
<!--                    <el-dropdown-item><i class="el-icon-message"></i>系统消息</el-dropdown-item>-->
<!--                    <el-dropdown-item><i class="el-icon-setting"></i>账号设置</el-dropdown-item>-->
                  <el-dropdown-item divided ><div @click="logout"><i class="el-icon-switch-button"></i>退出系统</div></el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </div>
    </div>
</template>
<script>
export default {
  props: {
    userImg: {
      type: String,
      require: true,
    },
  },
  methods: {
    logout() {
      console.log("logout")
      sessionStorage.setItem('token', null)
      this.$router.push('/login')
    },
    goBack() {
      this.$router.back()
    },
  },
};
</script>
<style>
</style>



