<template>
    <div>
      <el-container>
        <el-aside width="200px">
          <Aside :logoUrl="logoUrl" />
        </el-aside>
        <el-container>
          <el-header class="headerContainer" height="74px">
            <MainHeader :userImg="userImg" />
          </el-header>
          <el-main class="mainContainer">
            <router-view></router-view>
          </el-main>
        </el-container>
      </el-container>
    </div>
</template>
<script>
import "../assets/style/common.css"

import Aside from "../components/parts/aside.vue";
import MainHeader from "../components/parts/mainHeader.vue";

export default {
  components: {
    Aside,
    MainHeader
  },
  data() {
    return {
      logoUrl: require("../assets/img/logoW.svg"),
      userImg: require("../assets/img/userImg.svg"),
    };
  },
};
</script>
<style>
</style>



